/* running indicator */
@keyframes aui-pulse {
    50% {
        opacity: .5
    }
}
:where(.aui-md-running):empty::after,
:where(.aui-md-running) > :where(:not(ol):not(ul):not(pre)):last-child::after,
:where(.aui-md-running) > pre:last-child code::after,
:where(.aui-md-running)
  > :where(:is(ol, ul):last-child)
  > :where(li:last-child:not(:has(* > li)))::after,
:where(.aui-md-running)
  > :where(:is(ol, ul):last-child)
  > :where(li:last-child)
  > :where(:is(ol, ul):last-child)
  > :where(li:last-child:not(:has(* > li)))::after,
:where(.aui-md-running)
  > :where(:is(ol, ul):last-child)
  > :where(li:last-child)
  > :where(:is(ol, ul):last-child)
  > :where(li:last-child)
  > :where(:is(ol, ul):last-child)
  > :where(li:last-child)::after {
    animation: aui-pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
    font-family: ui-sans-serif, system-ui, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    --aui-content: '\25CF';
    content: var(--aui-content)
}
:where(.aui-md-running):empty:where([dir="ltr"], [dir="ltr"] *)::after,
:where(.aui-md-running) > :where(:not(ol):not(ul):not(pre)):last-child:where([dir="ltr"], [dir="ltr"] *)::after,
:where(.aui-md-running) > pre:last-child code:where([dir="ltr"], [dir="ltr"] *)::after,
:where(.aui-md-running)
  > :where(:is(ol, ul):last-child)
  > :where(li:last-child:not(:has(* > li))):where([dir="ltr"], [dir="ltr"] *)::after,
:where(.aui-md-running)
  > :where(:is(ol, ul):last-child)
  > :where(li:last-child)
  > :where(:is(ol, ul):last-child)
  > :where(li:last-child:not(:has(* > li))):where([dir="ltr"], [dir="ltr"] *)::after,
:where(.aui-md-running)
  > :where(:is(ol, ul):last-child)
  > :where(li:last-child)
  > :where(:is(ol, ul):last-child)
  > :where(li:last-child)
  > :where(:is(ol, ul):last-child)
  > :where(li:last-child):where([dir="ltr"], [dir="ltr"] *)::after {
    margin-left: 0.25rem
}
:where(.aui-md-running):empty:where([dir="rtl"], [dir="rtl"] *)::after,
:where(.aui-md-running) > :where(:not(ol):not(ul):not(pre)):last-child:where([dir="rtl"], [dir="rtl"] *)::after,
:where(.aui-md-running) > pre:last-child code:where([dir="rtl"], [dir="rtl"] *)::after,
:where(.aui-md-running)
  > :where(:is(ol, ul):last-child)
  > :where(li:last-child:not(:has(* > li))):where([dir="rtl"], [dir="rtl"] *)::after,
:where(.aui-md-running)
  > :where(:is(ol, ul):last-child)
  > :where(li:last-child)
  > :where(:is(ol, ul):last-child)
  > :where(li:last-child:not(:has(* > li))):where([dir="rtl"], [dir="rtl"] *)::after,
:where(.aui-md-running)
  > :where(:is(ol, ul):last-child)
  > :where(li:last-child)
  > :where(:is(ol, ul):last-child)
  > :where(li:last-child)
  > :where(:is(ol, ul):last-child)
  > :where(li:last-child):where([dir="rtl"], [dir="rtl"] *)::after {
    margin-right: 0.25rem
}

/* typography */
.aui-md-h1 {
    margin-bottom: 2rem;
    scroll-margin: 5rem;
    font-size: 2.25rem;
    line-height: 2.5rem;
    font-weight: 800;
    letter-spacing: -0.025em
}
.aui-md-h1:last-child {
    margin-bottom: 0px
}

.aui-md-h2 {
    margin-bottom: 1rem;
    margin-top: 2rem;
    scroll-margin: 5rem;
    font-size: 1.875rem;
    line-height: 2.25rem;
    font-weight: 600;
    letter-spacing: -0.025em
}

.aui-md-h2:first-child {
    margin-top: 0px
}

.aui-md-h2:last-child {
    margin-bottom: 0px
}

.aui-md-h3 {
    margin-bottom: 1rem;
    margin-top: 1.5rem;
    scroll-margin: 5rem;
    font-size: 1.5rem;
    line-height: 2rem;
    font-weight: 600;
    letter-spacing: -0.025em
}

.aui-md-h3:first-child {
    margin-top: 0px
}

.aui-md-h3:last-child {
    margin-bottom: 0px
}

.aui-md-h4 {
    margin-bottom: 1rem;
    margin-top: 1.5rem;
    scroll-margin: 5rem;
    font-size: 1.25rem;
    line-height: 1.75rem;
    font-weight: 600;
    letter-spacing: -0.025em
}

.aui-md-h4:first-child {
    margin-top: 0px
}

.aui-md-h4:last-child {
    margin-bottom: 0px
}

.aui-md-h5 {
    margin-top: 1rem;
    margin-bottom: 1rem;
    font-size: 1.125rem;
    line-height: 1.75rem;
    font-weight: 600
}

.aui-md-h5:first-child {
    margin-top: 0px
}

.aui-md-h5:last-child {
    margin-bottom: 0px
}

.aui-md-h6 {
    margin-top: 1rem;
    margin-bottom: 1rem;
    font-weight: 600
}

.aui-md-h6:first-child {
    margin-top: 0px
}

.aui-md-h6:last-child {
    margin-bottom: 0px
}

.aui-md-p {
    margin-bottom: 1.25rem;
    margin-top: 1.25rem;
    line-height: 1.75rem
}

.aui-md-p:first-child {
    margin-top: 0px
}

.aui-md-p:last-child {
    margin-bottom: 0px
}

.aui-md-a {
    font-weight: 500;
    color: hsl(var(--aui-primary));
    text-decoration-line: underline;
    text-underline-offset: 4px
}

.aui-md-blockquote {
    border-left-width: 2px;
    padding-left: 1.5rem;
    font-style: italic
}

.aui-md-ul {
    margin-top: 1.25rem;
    margin-bottom: 1.25rem;
    margin-left: 1.5rem;
    list-style-type: disc
}

.aui-md-ul>li {
    margin-top: 0.5rem
}

.aui-md-ol {
    margin-top: 1.25rem;
    margin-bottom: 1.25rem;
    margin-left: 1.5rem;
    list-style-type: decimal
}

.aui-md-ol>li {
    margin-top: 0.5rem
}

.aui-md-hr {
    margin-top: 1.25rem;
    margin-bottom: 1.25rem;
    border-bottom-width: 1px
}

.aui-md-table {
    margin-top: 1.25rem;
    margin-bottom: 1.25rem;
    width: 100%;
    border-collapse: separate;
    --aui-border-spacing-x: 0px;
    --aui-border-spacing-y: 0px;
    border-spacing: var(--aui-border-spacing-x) var(--aui-border-spacing-y);
    overflow-y: auto
}

.aui-md-th {
    background-color: hsl(var(--aui-muted));
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    text-align: left;
    font-weight: 700
}

.aui-md-th:first-child {
    border-top-left-radius: var(--aui-radius)
}

.aui-md-th:last-child {
    border-top-right-radius: var(--aui-radius)
}

.aui-md-th[align=center] {
    text-align: center
}

.aui-md-th[align=right] {
    text-align: right
}

.aui-md-td {
    border-bottom-width: 1px;
    border-left-width: 1px;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    text-align: left
}

.aui-md-td:last-child {
    border-right-width: 1px
}

.aui-md-td[align=center] {
    text-align: center
}

.aui-md-td[align=right] {
    text-align: right
}

.aui-md-tr {
    margin: 0px;
    border-bottom-width: 1px;
    padding: 0px
}

.aui-md-tr:first-child {
    border-top-width: 1px
}

.aui-md-tr:last-child>td:first-child {
    border-bottom-left-radius: var(--aui-radius)
}

.aui-md-tr:last-child>td:last-child {
    border-bottom-right-radius: var(--aui-radius)
}

.aui-md-sup>a {
    font-size: 0.75rem;
    line-height: 1rem;
    text-decoration-line: none
}

.aui-md-pre {
    overflow-x: auto;
    border-bottom-right-radius: var(--aui-radius);
    border-bottom-left-radius: var(--aui-radius);
    background-color: #000;
    padding: 1rem;
    --aui-text-opacity: 1;
    color: rgb(255 255 255 / var(--aui-text-opacity, 1))
}

.aui-md-inline-code {
    border-radius: 0.25rem;
    border-width: 1px;
    background-color: hsl(var(--aui-muted));
    font-weight: 600
}

.aui-code-header-root {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1rem;
    border-top-left-radius: var(--aui-radius);
    border-top-right-radius: var(--aui-radius);
    background-color: #18181b;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    font-size: 0.875rem;
    line-height: 1.25rem;
    font-weight: 600;
    --aui-text-opacity: 1;
    color: rgb(255 255 255 / var(--aui-text-opacity, 1))
}

.aui-code-header-language {
    text-transform: lowercase
}

.aui-code-header-language>span {
    font-size: 0.75rem;
    line-height: 1rem
}
